<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='readonly'
                       :showReturn='false'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>
    <staffOfficial :disabled='readonly'></staffOfficial>
  </div>
</template>

<script>
import { deleteStaffOfficial } from '@/service/api/staffmsg.js'
import staffOfficial from './staffOfficial'
export default {
  components: {
    staffOfficial
  },
  props: {},
  data () {
    return {
      readonly: false,
      list: {}
    };
  },
  computed: {},
  created () { },
  watch: {},
  mounted () {
    this.readonly = this.$store.state.iconstatus
    this.$route.meta.title = this.$store.getters.subPage_name
  },
  beforeDestroy () {

  },
  methods: {
    handleEdit () {
      this.readonly = !this.readonly
      this.$route.meta.title = this.$store.getters.subPage_name
      this.$store.commit('set_iconstatus', this.readonly)
    },
    handleDel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
        deleteStaffOfficial({ uuid: this.$store.state.uuid }).then(res => {
          if (res.code === '200') {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.push({
              name: 'staffOfficial'
            })
          }
        })
      })
    },
  },
};
</script>

