<template>
  <v-card>
    <v-card-text>
      <!-- <mrc_ess_staffMsg v-model="list.apply_staff_uuid"
                      :iconShow='true'
                      @changeList="changeList"></mrc_ess_staffMsg> -->
      <v-row>
        <v-col cols="12">
          <mrc_ess_prompt :label="$t('label_Regular_employee')+'*'"
                          v-if="!$store.state.uuid"
                          :rules='false'
                          :errMsg="$errors('list','staff_uuid',requiredRule)"
                          :disabled='disabled'
                          v-model="list.staff_uuid"
                          :obj="staff_probation_period">
          </mrc_ess_prompt>
          <!-- <mrc_ess_staffMsg :staffuuid='list.staff_uuid'
                          :listSelect='listSelect'
                          :disabled='true'></mrc_ess_staffMsg> -->
        </v-col>
        <v-col cols="6">
          <mrc_ess_datePicker :disabled='disabled'
                              :errMsg="$errors('list','join_com_date',requiredRule.concat([{rule:'beforeDate',msg:$t('verify_The_date_time_period_is_incorrect')}]))"
                              :label="$t('label_The_date_of_entry_to_the_company')+'*'"
                              v-model="list.join_com_date"></mrc_ess_datePicker>
        </v-col>
        <v-col cols="6">
          <mrc_ess_datePicker :disabled='disabled'
                              :label="$t('label_The_positive_date')+'*'"
                              :errMsg="$errors('list','change_regular_emp_date',requiredRule.concat([{rule:'afterDate',msg:$t('verify_The_date_time_period_is_incorrect')}]))"
                              v-model="list.change_regular_emp_date"></mrc_ess_datePicker>
        </v-col>
        <v-col cols="12">
          <!-- <v-text-field :label="$t('label_note')"
                      v-model="list.remarks"
                      :readonly="disabled">
        </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_note')"
                             :isNational="judegNational('remarks')"
                             v-model="list.remarks"
                             :readonly="disabled">
          </mrc_ess_multiText>
        </v-col>
      </v-row>
      <!-- <div style="background-color: #f5f5f5"
         class="mt-4"
         v-if="list.apply_staff_uuid">
      <v-col cols="6">
        <v-text-field label="原公司"
                      v-model="company_Name"
                      :readonly="disabled">
        </v-text-field>
      </v-col>
      <v-col cols="6">
        <mrc_ess_prompt label="原部门*"
                        :rules='false'
                        :disabled='true'
                        v-model="viewList.dept_uuid"
                        :obj="deptuuid">
        </mrc_ess_prompt>
      </v-col>
      <v-col cols="12">
        <mrc_ess_prompt label="原岗位"
                        :rules='false'
                        :disabled='true'
                        v-model="viewList.job_post_uuid"
                        :obj="job_post_uuid">
        </mrc_ess_prompt>
      </v-col>
      <v-col cols="12">
        <mrc_ess_staffMsg v-model="viewList.direct_leader_code"
                          :disabled='true'
                          :staffText='staffText'></mrc_ess_staffMsg>
      </v-col>
      <v-col cols="12">
        <mrc_ess_prompt label="原角色*"
                        :rules='false'
                        :disabled='true'
                        v-model="viewList.role_uuids"
                        :obj="spica">
        </mrc_ess_prompt>
      </v-col>
    </div> -->
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       @cancel='cancel'
                       :loading='btnIsLoading'
                       v-if='!disabled'></mrc_ess_submitBtn>
  </v-card>
</template>

<script> 
import {
  getStaffOfficialList
  , addStaffOfficial
  , updateStaffOfficial
  , deleteStaffOfficial
  , detailStaffOfficial
} from '@/service/api/staffmsg.js'
import { getBusinessList } from '@/service/api/prompt.js'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      list: {},
      btnIsLoading: false,
      viewList: {},
    };
  },
  computed: {

    staffText () {
      return [this.$t('label_Former_boss') + '*', '']
    },
    // deptuuid () {  //部门参照查询字段
    //   return { prompt_name: 'levelUuid_deptUuid', org_level_uuid: this.company_uuid }
    // },
    company_uuid () { //当前选中公司
      return this.$store.state.currentCompany
    },
    // company_Name () { //当前选中公司名字
    //   const items = this.$store.state.topcompanylist.filter(item => item.uuid == this.$store.state.currentCompany)
    //   return items[0].org_level_name
    // },
    // job_post_uuid () {  //岗位查询
    //   return { prompt_name: 'level-dept-post', dept_uuid: this.viewList.dept_uuid }
    // },
    // superior () { //上司高级参照
    //   return { prompt_name: 'boss', org_level_uuid: this.company_uuid, staff_uuid: this.$store.state.uuid }
    // },
    // spica () { //角色高级参照 
    //   return { prompt_name: 'level-role', org_level_uuid: this.company_uuid }
    // },
    staff_probation_period () {
      return { prompt_name: 'staff_probation_period', org_level_uuid: this.company_uuid }
    }
    // listSelect () {
    //   let data = []
    //   getBusinessList({ prompt_name: 'staff_probation_period', org_level_uuid: this.company_uuid }).then(res => {
    //     data = res.data
    //   })
    //   return data
    // }
  },
  created () { },
  watch: {},
  async mounted () {
    await this.national('mrbase_sys_staff_regular_employee')
    if (this.$store.state.uuid) {
      await this.getDetail()
      this.$route.meta.title = this.$store.state.subPage_name
    }
    else {

    }
  },

  methods: {
    paramsChange () { },
    changeList (val) {
      this.viewList = val
    },
    async getDetail () {
      const res = await detailStaffOfficial({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
        console.log(this.list);
      }
      else {

      }
    },
    async success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      if (this.$store.state.uuid) {
        updateStaffOfficial(this.$deletefield(null, this.list)).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.getDetail()
          }
          else {
            this.$store.commit('newsTips', this.$t('message_The_modification_failed') + res.msg)
          }
        })
      }
      else {
        addStaffOfficial(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_added_successfully'))
            this.$router.push({ name: 'staffLeave' })
          }
          else {
            this.$store.commit('newsTips', res.msg)
          }
        })
      }
    },
    cancel () {
      this.$myConfirm({
        content: '确认取消编辑'
      }).then(res => {
        this.$router.push({
          name: 'staffOfficial'
        })
      }).catch(err => { })
    },
  },
  validations: {
    list: {
      staff_uuid: {
        required
      },
      join_com_date: {
        required,
        beforeDate (value) {
          if (!this.list.change_regular_emp_date) return true
          const prevData = new Date(value).valueOf()
          const nextData = new Date(this.list.change_regular_emp_date).valueOf()
          return prevData < nextData
        }
      },
      change_regular_emp_date: {
        required,
        afterDate (value) {
          if (!this.list.join_com_date) return true
          const nextData = new Date(value).valueOf()
          const prevData = new Date(this.list.join_com_date).valueOf()
          return prevData < nextData
        }
      },
    }
  },
};
</script>

